@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
html {
  height: 100%;
}
body {
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
  max-width: 1440px;
  margin: 0 auto;
}
.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  font-weight: 400;
  background-color: #ffffff;
  margin: 0 auto;
  padding: 0;
  flex-grow: 1;
}
@media (min-width: 320px) {
  .page {
    max-width: 320px;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .page {
    max-width: 768px;
    margin: 0 auto;
  }
}
@media (min-width: 1440px) {
  .page {
    max-width: 1440px;
    margin: 0 auto;
  }
}
.main-container {
  min-width: 320px;
  max-width: 1440px;
  font-family: "Oswald", sans-serif;
  color: #444444;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.main-container__title {
  margin: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #000000;
}
.main-container__title-catalog {
  text-align: left;
  margin: 0;
  margin-left: 20px;
  margin-top: 27px;
  margin-bottom: 41px;
}
.main-container__title-form {
  margin-top: 27px;
  margin-bottom: 37px;
  margin-left: 20px;
  margin-right: 20px;
}
@media (min-width: 768px) {
  .main-container__title-form {
    margin-top: 53px;
    margin-bottom: 54px;
    margin-left: 70px;
    margin-right: 70px;
    font-size: 60px;
    line-height: 60px;
  }
  .main-container__title-catalog {
    margin-top: 53px;
    margin-bottom: 82px;
    margin-left: 30px;
    font-size: 60px;
    line-height: 60px;
  }
}
@media (min-width: 1440px) {
  .main-container__title-form {
    margin-top: 70px;
    margin-bottom: 56px;
    margin-left: 110px;
    margin-right: 110px;
  }
  .main-container__title-catalog {
    margin-top: 70px;
    margin-bottom: 84px;
    margin-left: 110px;
  }
}
.main-header {
  background-color: #ffffff;
  color: #000000;
  min-width: 320px;
}
.main-header__holder {
  padding: 10px 20px 18px;
  position: relative;
  box-sizing: border-box;
  height: 65px;
}
.main-header__logo {
  width: 191px;
  display: block;
}
.main-header__logo:hover {
  opacity: 0.8;
}
.main-header__logo:focus {
  opacity: 0.8;
}
.main-header__logo:active {
  opacity: 0.6;
}
.main-header__logo:disabled {
  opacity: 0.3;
}
.main-header__wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
}
@media (min-width: 768px) {
  .main-header {
    padding: 24px 30px 0 29px;
  }
  .main-header__logo {
    width: 174px;
    height: 52px;
  }
  .main-header__holder {
    padding: 0;
  }
  .main-header__wrapper {
    justify-content: space-between;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .navigation__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
  .main-header {
    padding: 53px 110px 0 110px;
  }
  .main-header__logo {
    width: 202px;
    height: 59px;
  }
  .main-header__logo-holder {
    width: 202px;
    height: 59px;
  }
  .main-header__logo-holder img {
    height: 100%;
    width: 100%;
  }
}
@media (max-width: 767px) {
  .navigation--opened .navigation {
    min-height: 198px;
  }
  .navigation--opened .navigation__wrapper {
    position: absolute;
    z-index: 10;
    min-height: 198px;
    width: 100%;
    background-color: #ffffff;
  }
  .navigation--closed .navigation__item {
    display: none;
  }
  .navigation--nojs .navigation__toggle {
    display: none;
  }
  .navigation--nojs .navigation__wrapper {
    position: static;
    min-height: 0;
  }
  .navigation--nojs .navigation__item {
    display: block;
  }
}
.navigation--closed .navigation__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 7px 0 0 #000000, 0 14px 0 0 #000000;
}
.navigation--closed .navigation__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 7px 0 0 rgba(0, 0, 0, 0.3), 0 14px 0 0 rgba(0, 0, 0, 0.3);
}
.navigation--opened .navigation__toggle::before,
.navigation--opened .navigation__toggle::after {
  content: "";
  position: absolute;
  right: 0;
  width: 25px;
  height: 2px;
  background-color: #000000;
}
.navigation--opened .navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.navigation--opened .navigation__toggle::after {
  transform: rotate(-45deg);
}
.navigation--opened .navigation__toggle:active::before,
.navigation--opened .navigation__toggle:active::after {
  background-color: rgba(0, 0, 0, 0.3);
}
.navigation__toggle {
  background-color: #ffffff;
  border: none;
  cursor: pointer;
  width: 24px;
  height: 16px;
  position: absolute;
  top: 24px;
  right: 20px;
}
.navigation__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.navigation__item {
  text-transform: uppercase;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  border-top: 1px solid #e6e6e6;
  text-align: center;
}
.navigation__link {
  display: block;
  color: #000000;
  text-decoration: none;
  padding: 22px 20px 23px;
}
.navigation__link:hover,
.navigation__link:focus {
  opacity: 0.6;
}
.navigation__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .navigation__list {
    width: 440px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 5px;
  }
  .navigation__item {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    border-top: none;
    position: relative;
  }
  .navigation__item--active .navigation__link {
    border-bottom: 2px solid #68b738;
  }
  .navigation__item--active .navigation__link:hover,
  .navigation__item--active .navigation__link:focus {
    opacity: 1;
  }
  .navigation__item--active .navigation__link:active {
    opacity: 1;
  }
  .navigation__link {
    padding: 0;
    padding-bottom: 8px;
  }
}
@media (min-width: 1440px) {
  .navigation {
    z-index: 2;
  }
  .navigation__list {
    width: 478px;
  }
  .navigation__link {
    color: #000000;
  }
  .navigation__link--white {
    color: #ffffff;
  }
  .navigation__item {
    font-size: 20px;
    line-height: 30px;
  }
  .navigation__item--active .navigation__link--white {
    border-bottom: 2px solid #ffffff;
  }
}
.promo {
  margin-bottom: 20px;
